@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/Rubik-Regular.woff") format("woff"), url("../resources/fonts/Rubik-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  src: url("../resources/fonts/Rubik-Medium.woff") format("woff"), url("../resources/fonts/Rubik-Medium.woff2") format("woff2");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  font-weight: 400;
}
body.scroll-disable {
  overflow-y: hidden;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}
@media (max-width: 1250.98px) {
  .container {
    max-width: 960px;
  }
}
@media (max-width: 960.98px) {
  .container {
    max-width: 720px;
  }
}
@media (max-width: 720.98px) {
  .container {
    max-width: 540px;
  }
}
@media (max-width: 480.98px) {
  .container {
    max-width: none;
  }
}

.title {
  font-size: 40px;
  font-weight: 500;
  line-height: 47px;
  color: var(--color-main);
}
@media (max-width: 720.98px) {
  .title {
    font-size: 24px;
    line-height: 28px;
  }
}

:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 1220px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-main: #102570;
  --color-black: #181818;
  --color-light-blue: #00A6FF;
  --grid-gap: 30px;
}

@media (max-width: 1250.98px) {
  .nav {
    display: none;
  }
}
.nav.is-open {
  position: absolute;
  top: 150px;
  right: 0;
  width: 100%;
  display: block;
  padding: 40px 0 30px;
  background: #EEF9FF;
  border-radius: 15px;
  z-index: 6;
}
@media (max-width: 960.98px) {
  .nav.is-open {
    top: 112px;
  }
}
.nav.is-open .nav-list {
  margin-bottom: 48px;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}
.nav-list {
  display: flex;
}
.nav-list__btn {
  display: none !important;
  margin: 0 auto;
}
@media (max-width: 1250.98px) {
  .nav-list__btn {
    display: block !important;
  }
}
.nav-item:not(:last-child) {
  margin-right: 20px;
}
.nav-item__link {
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  color: var(--color-main);
  transition: color 0.3s ease-in-out;
}
.nav-item__link:hover {
  color: var(--color-light-blue);
}

.btn {
  display: block;
  max-height: 56px;
  padding: 18.5px 50px 18.5px 25px;
  border: 1px solid var(--color-light-blue);
  border-radius: 35px;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  color: var(--color-main);
}

.form-container {
  max-width: 800px;
  margin: 0 auto;
}
@media (max-width: 960.98px) {
  .form-container {
    max-width: none;
    padding: 0 20px;
  }
}
.form-wrapper {
  margin-bottom: 44px;
  display: flex;
  justify-content: space-between;
  gap: 32px;
}
@media (max-width: 960.98px) {
  .form-wrapper {
    flex-direction: column;
  }
}
.form-item {
  position: relative;
  width: 50%;
}
@media (max-width: 960.98px) {
  .form-item {
    width: 100%;
  }
}
.form-item--w100 {
  width: 100%;
}
.form-submit {
  margin-top: 20px;
  display: flex;
  align-items: center;
}
@media (max-width: 720.98px) {
  .form-submit {
    flex-direction: column;
    gap: 20px;
  }
}
.form-submit__check {
  margin-left: 25px;
  display: flex;
  align-items: center;
}
.form-submit__text {
  display: block;
  margin-left: 7px;
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
  color: #181818;
}
.form-submit__text a {
  color: #181818;
  text-decoration: underline;
}
.form__label {
  position: absolute;
  top: -24px;
  left: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: var(--color-main);
}
.form__btn {
  padding: 18.5px 35px;
  background: #00A6FF;
  color: #fff;
  transition: background 0.3s ease-in-out;
}
.form__btn:hover {
  background: #0088D1;
}
@media (max-width: 720.98px) {
  .form__btn {
    max-width: 290px;
    width: 100%;
    order: 2;
  }
}

.form__input {
  width: 100%;
  height: 48px;
  padding: 14px;
  background: #fff;
  border-radius: 5px;
}

.form__textarea {
  height: 92px;
  resize: none;
}

.form__checkbox {
  width: 25px;
  height: 25px;
  background: #fff;
  border-radius: 3px;
  border: 1px solid var(--color-light-blue);
  cursor: pointer;
}
.form__checkbox:checked {
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='11' viewBox='0 0 14 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.944336 6.88905L5.111 9.66683L12.7499 1.3335' stroke='%23102570' stroke-width='2.01011'/%3E%3C/svg%3E ");
  background-position: center;
  background-repeat: no-repeat;
  z-index: 1;
}

.header-main {
  transition: all 0.3s ease-in-out;
}
.header-main.is-scrolling {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0px 19px 22.5px 0px rgba(161, 190, 206, 0.2509803922);
  z-index: 10;
}
.header-main.is-scrolling .header-logo {
  width: 72px;
  height: 72px;
}
.header-main.is-scrolling .header-info__upper {
  display: none;
}
.header-main.is-scrolling .header-info__mail {
  display: none;
}
.header-wrapper {
  position: relative;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1250.98px) {
  .header-wrapper {
    justify-content: flex-start;
  }
}
@media (max-width: 960.98px) {
  .header-logo {
    width: 72px;
    height: 72px;
  }
}
.header-info {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
}
@media (max-width: 1250.98px) {
  .header-info {
    margin-left: auto;
  }
}
.header-info__upper {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #393939;
  text-align: right;
}
@media (max-width: 720.98px) {
  .header-info__upper {
    font-size: 12px;
  }
}
.header-info__phone {
  position: relative;
  padding-left: 40px;
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  color: var(--color-main);
}
@media (max-width: 720.98px) {
  .header-info__phone {
    padding-left: 21px;
    font-size: 16px;
    line-height: 19px;
  }
}
