}
.header-info__mail {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: right;
  color: var(--color-light-blue);
}
@media (max-width: 960.98px) {
  .header-info__mail {
    display: none;
  }
}
.header-burger {
  display: none;
  height: 25px;
  margin-left: 15px;
  cursor: pointer;
}
@media (max-width: 1250.98px) {
  .header-burger {
    display: block;
  }
}
.header-burger__line {
  position: relative;
  display: block;
  width: 35px;
  transform: translateY(8px);
  border-bottom: 3px solid var(--color-main);
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 720.98px) {
  .header-burger__line {
    transform: translateY(12px);
  }
}
.header-burger__line.is-open {
  border: none;
}
.header-burger__line.is-open::after {
  top: 2px;
  transform: rotate(45deg);
}
.header-burger__line.is-open::before {
  bottom: -5px;
  transform: rotate(-45deg);
}
.header-burger__line::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 8px;
  width: 100%;
  height: 3px;
  border-bottom: 3px solid var(--color-main);
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
}
.header-burger__line::after {
  content: "";
  position: absolute;
  top: 11px;
  left: 0;
  width: 100%;
  height: 3px;
  border-bottom: 3px solid var(--color-main);
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
}

.hero {
  margin-bottom: 70px;
}
@media (max-width: 720.98px) {
  .hero {
    margin-bottom: 40px;
  }
}
.hero-wrapper {
  padding: 27px 60px;
  display: flex;
  justify-content: space-between;
  background: #EEF9FF;
  border-radius: 15px;
}
@media (max-width: 1250.98px) {
  .hero-wrapper {
    gap: 20px;
  }
}
@media (max-width: 960.98px) {
  .hero-wrapper {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }
}
@media (max-width: 720.98px) {
  .hero-wrapper {
    gap: 37px;
  }
}
@media (max-width: 480.98px) {
  .hero-wrapper {
    padding: 30px 20px;
  }
}
.hero-info {
  max-width: 630px;
}
@media (max-width: 1250.98px) {
  .hero-info {
    max-width: 400px;
  }
}
@media (max-width: 960.98px) {
  .hero-info {
    max-width: none;
    text-align: center;
  }
}
.hero-info__title {
  margin-bottom: 15px;
  font-size: 48px;
  font-weight: 500;
  line-height: 53px;
  color: var(--color-main);
}
@media (max-width: 720.98px) {
  .hero-info__title {
    font-size: 28px;
    line-height: 30px;
  }
}
.hero-info__descr {
  margin-bottom: 30px;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  color: #181818;
}
@media (max-width: 720.98px) {
  .hero-info__descr {
    font-size: 15px;
    line-height: 18px;
  }
}
.hero-info__btn {
  position: relative;
  max-width: 260px;
  padding-right: 40px;
  transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
}
@media (max-width: 960.98px) {
  .hero-info__btn {
    margin: 0 auto;
  }
}
.hero-info__btn::before {
  content: "";
  position: absolute;
  top: 16px;
  right: 20px;
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M17.7071 6.29289C18.0976 6.68342 18.0976 7.31658 17.7071 7.70711L7.70711 17.7071C7.31658 18.0976 6.68342 18.0976 6.29289 17.7071C5.90237 17.3166 5.90237 16.6834 6.29289 16.2929L16.2929 6.29289C16.6834 5.90237 17.3166 5.90237 17.7071 6.29289Z' fill='%23102570'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6 7C6 6.44772 6.44772 6 7 6H17C17.5523 6 18 6.44772 18 7V17C18 17.5523 17.5523 18 17 18C16.4477 18 16 17.5523 16 17V8H7C6.44772 8 6 7.55228 6 7Z' fill='%23102570'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
  transition: background-image 0.3s ease-in-out;
}
.hero-info__btn:hover {
  background: var(--color-light-blue);
  color: #fff;
}
.hero-info__btn:hover::before {
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M17.7071 6.29289C18.0976 6.68342 18.0976 7.31658 17.7071 7.70711L7.70711 17.7071C7.31658 18.0976 6.68342 18.0976 6.29289 17.7071C5.90237 17.3166 5.90237 16.6834 6.29289 16.2929L16.2929 6.29289C16.6834 5.90237 17.3166 5.90237 17.7071 6.29289Z' fill='white'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6 7C6 6.44772 6.44772 6 7 6H17C17.5523 6 18 6.44772 18 7V17C18 17.5523 17.5523 18 17 18C16.4477 18 16 17.5523 16 17V8H7C6.44772 8 6 7.55228 6 7Z' fill='white'/%3E%3C/svg%3E ");
}

.advantages {
  margin-bottom: 70px;
}
@media (max-width: 720.98px) {
  .advantages {
    margin-bottom: 40px;
  }
}
.advantages__title {
  margin-bottom: 44px;
  text-align: center;
}
.advantages-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 33px;
}
@media (max-width: 960.98px) {
  .advantages-list {
